import React from 'react'
import {
  Slider,
  SliderTrack,
  SliderFilledTrack,
  SliderThumb, ChakraProvider, 
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverBody,
  PopoverArrow,
  IconButton,
  Img,
  Menu,
  MenuButton,
  MenuList,
  MenuItem
} from '@chakra-ui/react'
import Volume from './volume.svg'


function SliderV({ value, updateGlobalNum }) {
  return (
    <React.StrictMode>
      <ChakraProvider>
        <Menu closeOnSelect={false} autoSelect={false} computePositionOnMount={false}>
          <MenuButton boxSize='2em' mt='13px' ml='auto' mr='-25px' target="_blank" variant="unstyled" >
            <Img src={Volume} alt='armenia2'/>
          </MenuButton>
          <MenuList minWidth='40px'>
            <MenuItem>
            <Slider 
                orientation='vertical' 
                onChange={updateGlobalNum}
                minH={32}
                max={100}
                min={0}
                value={value}
            >
            <SliderTrack>
                <SliderFilledTrack/>
            </SliderTrack>
            <SliderThumb />
            </Slider>
              </MenuItem>
          </MenuList>
        </Menu>
      </ChakraProvider>
    </React.StrictMode>
  )
}

export default SliderV
